.story-section {
	position: relative;
	.story {
		position: relative;
		background-color: var(--color-custom-1);
		color: var(--color-light);
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
	}
	.story-content {
		position: relative;
		&::before{
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 80%;
			background-color: var(--color-main);
			opacity: .3;
			border-radius: 28px;
			content: "";
		}
	}
	.story-description {
		position: relative;
		text-align: center;
		padding: 3rem 2rem 2rem;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 3/4;
		padding: 2px;
		&::before{
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background-color: var(--color-light);
			mask-image: url(../images/mask-2.png);
			mask-size: 100% 100%;
			mask-repeat: no-repeat;
			content: "";
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				object-fit: cover;
				width: 100%;
				height: 100%;
				mask-image: url(../images/mask-2.png);
				mask-size: 100% 100%;
				mask-repeat: no-repeat;
			}
		}
	}
}
