.header-section {
	position: relative;
	background-color: var(--color-primary);
	color: var(--color-light);
	width: 100%;
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
	}
	.header-content {
		position: relative;
		height: 100vh;
		.header {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 35%;
			height: 100%;
			margin: 0 auto;
			padding: 1rem 0 3rem;
			@include max-xl{
				width: 40%;
			}
			@include max-lg{
				width: 60%;
			}
			@include max-md{
				width: 50%;
			}
			@include max-sm{
				width: 95%;
			}
			.header-title {
				position: relative;
				text-align: center;
				&-content {
					position: relative;
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}
